import React, { useState } from "react";
import styles from "./Settings.module.scss";

const Settings = () => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "ivan@example.com",
    age: "25",
    city: "",
    country: "",
    profession: "",
    favoriteAlcohol: "",
    hobbies: "",
    about: " .",
    oldPassword: "",
    newPassword: "",
    confirmPassword: ""
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value
    }));
  };

  return (
    <div className={styles.settingsContainer}>
      <h1> </h1>
      <div className={styles.section}>
        {Object.entries({ firstName: "", lastName: "", email: "Email" }).map(([key, label]) => (
          <div key={key} className={styles.inputGroup}>
            <label>{label}</label>
            <input type="text" name={key} value={formData[key as keyof typeof formData]} onChange={handleChange} />
            <button></button>
          </div>
        ))}
      </div>
      <h2> </h2>
      {Object.entries({ age: "", city: "", country: "" }).map(([key, label]) => (
        <div key={key} className={styles.inputGroup}>
          <label>{label}</label>
          <select name={key} value={formData[key as keyof typeof formData]} onChange={handleChange}>
            {["18", "25", "30", "40"].map((opt) => (
              <option key={opt} value={opt}>{opt}</option>
            ))}
          </select>
        </div>
      ))}
      {Object.entries({ profession: "", favoriteAlcohol: " ", hobbies: "", about: "  " }).map(([key, label]) => (
        <div key={key} className={styles.inputGroup}>
          <label>{label}</label>
          <input type="text" name={key} value={formData[key as keyof typeof formData]} onChange={handleChange} />
        </div>
      ))}
      <h2></h2>
      <p>    ,    </p>
      <div className={styles.inputGroup}>
        <label> </label>
        <input type="password" name="oldPassword" value={formData.oldPassword} onChange={handleChange} />
        <a href="#"> ?</a>
      </div>
      <div className={styles.inputGroup}>
        <label> </label>
        <input type="password" name="newPassword" value={formData.newPassword} onChange={handleChange} />
      </div>
      <div className={styles.inputGroup}>
        <label>  </label>
        <input type="password" name="confirmPassword" value={formData.confirmPassword} onChange={handleChange} />
      </div>
      <button className={styles.submitButton}> </button>
    </div>
  );
};

export default Settings;
