import { Link, Outlet } from 'react-router-dom';
import styles from './ProfileLayout.module.scss';

const ProfileLayout = () => {
  return (
    <div className={styles.container}>
      {/* Navbar */}
      <header className={styles.navbar}>
        <div className={styles.navbarLeft}>
          <Link to="/"></Link>
          <Link to="/profile"></Link>
        </div>
      </header>

      {/*   */}
      <main className={styles.mainContent}>
        {/*  (1/5 ) */}
        <aside className={styles.sidebar}>
          <ul>
            <li><Link to="/profile"> </Link></li>
            <li><Link to="/profile/friends"> </Link></li>
            <li><Link to="/profile/follows"> </Link></li>
            <li><Link to="/profile/chats"> </Link></li>
            <li><Link to="/profile/news-feed"> </Link></li>
            <li><Link to="/profile/reviews"> </Link></li>
            <li><Link to="/profile/basket"> </Link></li>
            <li><Link to="/profile/buy-history">  </Link></li>
            <li><Link to="/profile/settings"> </Link></li>
            <li><Link to="/catalog"> </Link></li>
            <li><Link to="/logout"></Link></li>
          </ul>
        </aside>

        {/*   (4/5 ) */}
        <section className={styles.profileContent}>
          <Outlet /> {/*      */}
        </section>
      </main>
    </div>
  );
};

export default ProfileLayout;
